<template>
    <div>
        <div id="editor-md" style="z-index: 5">
            <textarea style="display:none;"/>
        </div>
        <div id="md-html" style="z-index: 0">
            <textarea style="display:none;"/>
        </div>
    </div>
</template>

<script>
    import $script from 'scriptjs';

    export default {
        name: 'EditorMD',
        components: {},
        props: {
            readOnly: {
                type: Boolean,
                default: true
            },
            preSetContent: {
                type: String,
            },
            editorPath: {
                type: String,
                default: '/editormd/',
            },
        },
        data() {
            return {
                instance: null,
                htmlInstance: null
            }
        },
        watch: {
            preSetContent(val) {
                //keep-alivemarkdown
                //
                if (this.instance != null) {
                    console.log("", val.length)
                    this.instance.setMarkdown(val)
                }
                if (this.htmlInstance != null) {
                    console.log("", val.length)
                    this.initEditor()
                }
            },
        },
        created() {
        },
        mounted() {
            // async loading js dependencies
            // editormd depdend on jquery and zepto
            $script([
                    `${this.editorPath}js/jquery.min.js`,
                    `${this.editorPath}js/zepto.min.js`,
                    `${this.editorPath}lib/marked.min.js`,
                    `${this.editorPath}lib/prettify.min.js`,

                    `${this.editorPath}lib/raphael.min.js`,
                    `${this.editorPath}lib/underscore.min.js`,
                ], () => {
                    console.log("js1")
                    $script([
                            `${this.editorPath}lib/sequence-diagram.min.js`,
                            `${this.editorPath}lib/sequence-diagram.min.js`,
                            `${this.editorPath}lib/flowchart.min.js`,
                            `${this.editorPath}lib/jquery.flowchart.min.js`
                        ], () => {
                            console.log("js2")
                            $script(`${this.editorPath}/editormd.min.js`, () => {
                                    console.log("initEditor before")
                                    this.initEditor();
                                    console.log("initEditor after")
                                }
                            )
                        }
                    )
                }
            );
        },
        beforeDestroy() {
        },
        methods: {
            initEditor() {
                let _this = this
                let theme = ""
                let editorTheme = ""
                if (this.$vuetify.theme.dark) {
                    theme = "dark"
                    editorTheme = "pastel-on-dark"
                }
                console.log("theme", theme)
                // eslint-disable-next-line
                this.$nextTick((editorMD = window.editormd) => {
                    if (editorMD) {
                        // Vue  DOM template  script 
                        //  nextTick  editor.md
                        if (this.readOnly) {
                            this.htmlInstance = editorMD.markdownToHTML("md-html", {
                                    // path: "/editormd/lib/",
                                    // markdown: "# md-html",
                                    markdown: _this.preSetContent,
                                    theme: theme,
                                    previewTheme: theme,
                                    editorTheme: editorTheme,
                                    htmlDecode: "style,script,iframe",  // you can filter tags decode
                                    emoji: true,
                                    taskList: true,
                                    tex: true,  // 
                                    flowChart: true,  // 
                                    sequenceDiagram: true,  // 
                                    onload: function () {
                                        console.log('md-html', this);
                                        //this.fullscreen();
                                        //this.unwatch();
                                        //this.watch().fullscreen();
                                        this.setMarkdown(_this.preSetContent);
                                        //this.width("100%");
                                        //this.height(480);
                                        //this.resize("100%", 640);
                                    },
                                },
                            )
                        } else {
                            this.instance = editorMD('editor-md', {
                                width: "100%",
                                height: 700,
                                //
                                // autoHeight: true,
                                path: "/editormd/lib/",
                                placeholder: "...",
                                //
                                // toolbar: false,
                                //
                                // readOnly: false,
                                // saveHTMLToTextarea: false,
                                //
                                // watch: true,
                                styleActiveLine: false,       // disable active line
                                // lineNumbers: false,       // hide line numbers
                                theme: theme,
                                previewTheme: theme,
                                editorTheme: editorTheme,
                                codeFold: true,
                                //syncScrolling : false,
                                // saveHTMLToTextarea: true,    //  HTML  Textarea
                                searchReplace: true,
                                // htmlDecode: "style,script,iframe|on*",            //  HTML 
                                //previewCodeHighlight : false, //  HTML 
                                emoji: true,
                                taskList: true,
                                tocm: true,         // Using [TOCM]
                                tex: true,                   // TeX
                                flowChart: true,             // 
                                sequenceDiagram: true,       // /,
                                //dialogLockScreen : false,   // true
                                //dialogShowMask : false,     // true
                                //dialogDraggable : false,    // true
                                //dialogMaskOpacity : 0.4,    // 0.1
                                //dialogMaskBgColor : "#000", // #fff
                                imageUpload: true,
                                imageFormats: ["jpg", "jpeg", "gif", "png", "bmp", "webp"],
                                imageUploadURL: this.API.URL.UPLOAD_ONE_FILE,
                                onload: function () {
                                    console.log('editor-md', this);
                                    //this.fullscreen();
                                    //this.unwatch();
                                    //this.watch().fullscreen();

                                    this.setMarkdown(_this.preSetContent);
                                    //this.width("100%");
                                    //this.height(480);
                                    //this.resize("100%", 640);
                                },
                                onchange: function () {
                                    _this.$emit("transferContent", this.getMarkdown())
                                }
                            })
                        }
                    }
                });
            },
        },
    };
</script>

<style scoped>

</style>